<style lang="less" scoped>

 .piece{
   width: 10px;
   height: 10px;
   margin: 4px;
   background-color: grey;
   display: inline-block;
 }
 &.completed {
   background-color: green;
 }
</style>
<template>
  <div>
    <dl v-if="task">
      <dt>任务名</dt>
      <dd>{{ getFilename(task) }}</dd>
      <dt>大小</dt>
      <dd></dd>
      <dt>进度</dt>
      <dd></dd>
      <dt>下载地址</dt>
      <dd></dd>
      <dt>文件列表</dt>
      <dd>
        <ul>
          <li v-for="file of task.files" :title="file.path"  :key="file.path">{{file.path}}</li>
        </ul>
      </dd>
      <dt>区块信息</dt>
      <dd>
        <span v-for="i of Number(task.numPieces)" 
        :key="i" class="piece" :class="{'completed': bitfield[i - 1] == '1' }">  </span>
      </dd>
    </dl>
  </div>
</template>

