  name: 'TaskDetail',
  data() {
    return {
      task: null,
      bitfield: [],
    }
  },
  async mounted() {
    this.task = await window.aria2.tellStatus(this.$route.params.gid)
    this.bitfield = this.task.bitfield.split('').map(it => parseInt(it, 16)
